import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { CoreService } from '../../core/core.service';
import {
  DashboardCountDto,
  DashboardService,
  FriggResponse,
  GetCurrentLoginInformationsOutput,
  SessionService,
  SystemService
} from '../../proxies';

@Component({
  selector: 'frigg-dashboard',
  templateUrl: 'dashboard.component.html',
  styleUrls: [ 'dashboard.component.scss' ],
  encapsulation: ViewEncapsulation.None
})
export class DashboardComponent implements OnInit {
  loginInfo: GetCurrentLoginInformationsOutput;

  dashboardCount: DashboardCountDto;

  constructor(private session: SessionService,
              private dashboardService: DashboardService,
              private systemService: SystemService,
              private core: CoreService) {
  }

  get greeting(): Observable<string> {
    if (this.loginInfo) {
      return this.core.translation.stream('frigg.welcome', {name: this.loginInfo.user.name});
    } else {
      return Observable.of('');
    }
  }

  ngOnInit(): void {
    this.session.getCurrentLoginInformations()
      .subscribe((response: FriggResponse<GetCurrentLoginInformationsOutput>) => {
        if (response.success) {
          this.loginInfo = response.result;
          this.loadDashboardCount();
          this.core.shell.setHeader(''); // this.greeting);
          this.core.shell.setNavigateText('');
          this.core.shell.setNavigateUrl('');
        }
      });
  }

  private loadDashboardCount(): void {
    this.dashboardService.getDashboardCount()
      .subscribe((response: FriggResponse<DashboardCountDto>) => {
        if (response.success) {
          this.dashboardCount = response.result;
        }
      });
  }

  restartActors(): void {
    this.systemService.restartActors()
      .subscribe((response: FriggResponse<boolean>) => {
        if (response.success) {
          this.core.toast.success('Actors restarted');
        }
      });
  }
}



